.App {
  text-align: center;
  color: white;
  overflow-x: hidden;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.entire-page {
  height: 400rem;
}

.splash-right {
  background-color: #2342A2;;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  /* width: 50%; */
  margin-left: auto;
}

.mg-left {
  margin-left: 3rem
}

.splash-left {
  background-color: white;
  font-size: 3rem;
  width: 10px;
  height: 10px;
  display: flex;
  padding-left: 4rem;
  color: black;
  position: absolute;
  min-height: 100vh;
  width: 50%;
  align-items: center;
  justify-content: center;
  /* flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-right: auto;  */
}

.header-text-block {
  display: flex;
  justify-content: center;
  text-align: center;
}
.header-text-block-2 {
  display: flex;
  justify-content: left;
  text-align: center;
  color: black;
  /* font-size: 7rem; */
  /* background-color: red; */
}

.header-copy {
  display: flex;
  font-size: 3rem;
  /* position: absolute; */
}
.subheader-copy {
  display: flex;
  /* position: absolute; */
  font-size: 1rem;
  margin-top: 3rem;
  width: 80%;
  justify-content: flex-start;
  text-align: left;
}

.splash-image {
  object-fit: cover;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

